/******************************************************************

Stylesheet: Events

******************************************************************/

/*********************
 * GLOBAL
 ********************/

.event-container,
.events-container {
    margin-bottom: 45px;

    .date {
        color: $ofs-danger;
    }
}

.event-container {
    .date {
        margin-top: 30px;
        margin-bottom: 25px;
    }
}

.events-container {
    text-align: left;

    &--item {
        & + .events-container--item {
            margin-top: 15px;
        }
    }
}

.latest-events {
    padding: 25px 0;
}

body.single-events {
    .breadcrumb { margin-bottom: 30px; }
}

/*********************
 * PREVIEW
 ********************/

.event-preview {
    @include clearfix;

    background: $ofs-lightgrey;
    border-radius: $border-radius-xs;
    display: block;
    padding: 10px;
    
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.25);

    &--date,
    &--content {
        display: inline-block;
        vertical-align: top;
    }

    &--content {
        width: calc(100% - 65px);
        padding: 0 0 0 15px;

        p {
            margin-bottom: 0;
        }
    }

    p {
        @include fontSize(12px);
        color: $ofs-darkblue;
    }

    .event-preview--title {
        @include fontSize(14px);
        font-weight: $fw-md;
    }

    .readmore {
        margin-top: 5px;
        display: block;
    }
}

/*********************
 * MEDIA QUERIES
 ********************/

// 576
@include media-breakpoint-up(xs) {
    .events-container {
        display: flex;
        align-content: flex-start;
        flex-wrap: wrap;
        margin: 15px -1% 45px;

        &--item {
            width: 48%;
            margin: 1%;

            & + .events-container--item { 
                margin: 1%; 
                // margin-top: 0;
            }

            .card-block {
                position: relative;
                padding: 20px 20px 40px;

                .readmore {
                    position: absolute; bottom: 20px; 
                }
            }
        }
    }
}

// 768
@include media-breakpoint-up(sm) {
    .events-container {
        margin-top: 0;
        // padding-bottom: 40px;

        &--item {
            width: 31%;
        }
    }

    body.home .latest-events {
        padding-top: 40px;
        padding-bottom: 65px;
    }
    
    body.single-events {
        .events-container {
            margin-bottom: 65px;
        }
    }
}

// 992
@include media-breakpoint-up(md) {}

// 1200
@include media-breakpoint-up(lg) {}

// 1600
@include media-breakpoint-up(xlg) {
    body.home .latest-events {
        padding-top: 60px;
        padding-bottom: 85px;
    }
}
