
/******************************************************************

Stylesheet: Hamburger

******************************************************************/

@mixin slice {
	position: absolute; left: 0;
	height: 1px; width: 100%;
	background: $ofs-lightgrey;
	display: block;
	transition: all $duration ease-in-out;
}

.ui.hamburger {
    position: absolute; top: 50%; right: 20px;
    transform: translateY(-50%);
	width: 25px; height: 17px;

	&::before, &::after {
		@include slice;
		content: '';

        top: 0.4375rem;
        left: 50%;
        width: 0%;
	}


	span {
		@include slice;
		top: 0.4375rem;
	}

    span:first-child { transform: rotate(45deg); }
    span:last-child { transform: rotate(-45deg); }

    // close state
    &.collapsed {
    	&::before { top: 0 ; }
    	&::after { top: 0.875rem ; }
        &::before, &::after {
            left: 0;
            width: 100%;
        }

        span { transform: rotate(0); }
    }

	&:hover {
		cursor: pointer;
	}
}

/*********************
 * MEDIA QUERIES
 ********************/

@include media-breakpoint-up(xs) {
    // pink

    .ui.hamburger {
        right: 25px;
    }
}

@include media-breakpoint-up(sm) {
    // orange

    .ui.hamburger {
        right: 25px;
    }
}

@include media-breakpoint-up(md) {
    // red;

	.ui.hamburger { display: none; }
}

@include media-breakpoint-up(lg) {
    // green;
}

@include media-breakpoint-up(xlg) {
    // blue;
}
