
/******************************************************************

Stylesheet: Carousels

******************************************************************/

/*********************
 * GLOBAL
 ********************/

.owl-carousel {
    position: relative;
}

.owl-nav {
    position: absolute; top: 50%;
    transform: translateY(-50%);
    width: 100%;
    padding: 0 15px;

    .icon-round {
        overflow: hidden;
        cursor: pointer;
        width: 30px; height: 30px;
        line-height: 30px;
        border: none;
        background: $white;
        color: $black;
        box-shadow: 0 0 5px 0 rgba(black, .5);

        &::before {
            width: 30px; height: 30px;
            display: block;
        }

        &.disabled {
            opacity: .5;
            cursor: not-allowed;
        }
    }

    .icon-chevron-left { float: left; }
    .icon-chevron-right { float: right; }
}

.owl-theme {
    &.dots-hover .owl-dots {
        position: absolute; bottom: 0;
        width: 100%;

        .owl-dot {
            &:hover span,
            &.active span {
                background: $ofs-success;
            }

            span {
                background: $white;
            }
        }
    }

    .owl-dots {
        .owl-dot {
            &:hover span,
            &.active span {
                background: $ofs-darkred;
            }

            span {
                background: $ofs-red;
                transition: background $duration;
            }
        }
    }
}


/*********************
 * FEATURED CAROUSEL
 ********************/

.featured-carousel {
    margin-bottom: 15px;

    li {
        padding: 3px;
    }

    .card {
        border-radius: $border-radius-xs;
        margin-bottom: 0;
        @include ratio(45%);

        img {
            @include absolute-center;
            width: 80%;
        }
    }
}

/*********************
 * MEDIA QUERIES
 ********************/

// 576
// @include media-breakpoint-up(xs) {}

// 768
@include media-breakpoint-up(sm) {
    .featured-carousel {
        margin: 0 45px 25px;

        ul { position: relative; }

        .owl-nav {
            margin-top: 0; padding: 0;

            .icon-arrow-left,
            .icon-arrow-right {
                position: absolute; top: 50%;
                transform: translateY(-50%);
            }

            .icon-arrow-left { left: -45px; }
            .icon-arrow-right { right: -45px; }
        }
    }
}

// 992
@include media-breakpoint-up(md) {
    .home {
        .hero {
            .owl-carousel {
                max-width: 810px;
                margin: 0 auto;
            }
            .owl-nav {
                .icon-arrow-left { position: absolute; left: -60px; top: calc(50% - 15px); }
                .icon-arrow-right { position: absolute; right: -60px; top: calc(50% - 15px); }
            }
        }
    }
}

