/******************************************************************

Stylesheet: Modals

******************************************************************/

/*********************
 * GLOBAL
 ********************/

.modal {
    z-index: 10000;
    
    .btn-close { 
        position: absolute; right: 15px; top: 15px;
        font-size: 24px;
    }
    
    &-content {
        overflow: hidden;
        box-shadow: 0 0 15px 0 rgba(black, .5);
    }
    
    &-header,
    &-footer {
        position: relative;
        background: $ofs-lightgrey;
        border: none;
        padding: 30px;

        p {
            margin-bottom: 0;

        }
    }
    
    &-body {
        padding: 30px;
    }

    &-title {
        width: 100%;
        text-align: center;
    }
    
    &-backdrop {
        background: $ofs-darkblue;
    }

    // modals
    &-contact {
        .alert {
            display: none;
            
            &.is-visible {
                display: block;
            }
        }
    }
}

