/******************************************************************

Stylesheet: Account

******************************************************************/

/*********************
 * GLOBAL
 ********************/

body.woocommerce-account {
    .woocommerce {
        .woocommerce-message,
        .woocommerce-error,
        .woocommerce-info {
            margin-bottom: 15px;
        }
    }

    .woocommerce-form-login {
        .ui.checkbox { margin-top: 25px; }
    }
}

.woocommerce-MyAccount-navigation {
    background: $ofs-lightgrey;
    border-radius: $border-radius-xs;
    padding: 15px;
    margin-bottom: 25px;

    ul {
        @include cancel-list;

        li + li {
            margin-top: 5px;
        }

        .is-active a {
            font-weight: $fw-md;
        }

        a {
            @include cancel-link;
            display: block;
            color: $ofs-darkblue;

            transition: background $duration, color $duration;

            &:hover {
                font-weight: $fw-md;
            }
        }
    }
}

.woocommerce-MyAccount-content {
    .woocommerce-Message .woocommerce-Button { display: none; }

    .woocommerce-Address-title a.edit {
        padding-left: 10px;
        color: $ofs-red;
    }

    form {
        h3 {
            @include ofs-subtitle-secondary;
        }

        h3, legend {
            margin-bottom: 25px;
        }

        &.edit-account {
            .h3 {
                padding-top: 15px;
            }
        }
    }
}

.woocommerce-ResetPassword {
    .form-group {
        width: 100%;
    }
}

.woocommerce-Address {
    display: block;
    width: 100%;
    max-width: 100%;
    flex: none;
    padding: 0;
}

/*********************
 * ORDERS
 ********************/

.woocommerce-order-details {
    .ofs-subtitle-secondary {
        margin-bottom: 25px;
    }

    .order-details-totals {
        width: 100%;
        margin-top: 20px; padding: 15px;
        border: 1px solid #e5e5e5;

        table {
            width: 100%;

            tr:first-child th,
            tr:first-child td { border-top: none; }

            tr th, tr td {
                padding: 9px 9px 9px 0;
                border-top: 1px solid #ebe9eb;
            }
        }

        .order-again {
            margin-top: 15px;
            margin-bottom: 0;

            .btn {
                padding-left: 15px;
                padding-right: 15px;
            }
        }
    }
}

/*********************
 * MEDIA QUERIES
 ********************/

// 576
@include media-breakpoint-up(xs) {
    .woocommerce-order-details {
        .order-details-totals {
            @include clearfix;
            padding: 25px;

            .order-again {
                float: right;

                .btn {
                    padding-left: 30px;
                    padding-right: 30px;
                }
            }
        }
    }
}

// 768
@include media-breakpoint-up(sm) {
    body.logged-in.woocommerce-account {
        .woocommerce > .woocommerce-message,
        .woocommerce-MyAccount-content,
        .woocommerce-error {
            width: calc(100% - 225px);
            float: right;
        }
    }

    .woocommerce-MyAccount-navigation {
        width: 200px;
        float: left;
    }

    .woocommerce-ResetPassword {
        .form-group {
            width: auto;

            &.form-row-first {
                width: 400px;
            }

            button[type=submit] {
                margin-top: 25px;
                margin-left: 15px;
            }
        }
    }
}

// 992
@include media-breakpoint-up(md) {
    .woocommerce-Addresses {
        display: flex;
        flex: auto;
        flex-wrap: wrap;

        .woocommerce-Address {
            max-width: 50%;
            flex: 0 0 auto;
        }
    }

    .woocommerce-MyAccount-content {
        form {
            #billing_first_name_field,
            #billing_last_name_field,
            #billing_city_field,
            #billing_postcode_field,
            #shipping_first_name_field,
            #shipping_last_name_field,
            #shipping_city_field,
            #shipping_postcode_field,
            .inline-group {
                display: inline-block;
                width: calc(50% - 5px);
                vertical-align: top;
            }

            #billing_last_name_field,
            #shipping_last_name_field,
            #shipping_postcode_field,
            .inline-group:nth-child(even) {
                margin-left: 10px;
            }

            #billing_postcode_field { margin-right: 10px; }

            #shipping_city_field { float: left; }

            .inline-group:nth-child(even) {
                float: right;
            }
        }
    }

    .woocommerce-order-details {
        .order-details-totals {
            max-width: 500px;
            width: 50%;
            float: right;
        }
    }
}

