body.styleguide {
    position: relative;
    padding: 15px;
    background: $white;
}

#styleguide {
    .aside {
        display: none;

        li {
            border-bottom: 1px solid #b7b7b7;

            a {
                color: $ofs-red;
                display: block;
                padding: $spacing-xs;
            }
        }
    }

    .section + .section { margin-top: 25px; }
    .section {
        h2 {
            display: block;
        }
        
        u, i, strong, b, a {
            margin-bottom: 10px;
            display: block;
        }

        a {
            display: inline-block;
        }
    }

    .section-title {
        border-bottom: 1px solid #ccc;
        padding-bottom: 5px;
    }

    #cards {
        @include clearfix;
        
        .card { width: 250px; }
    }
}

/*********************
 * MEDIA QUERIES
 ********************/

// 576
@include media-breakpoint-up(xs) {}

// 768
@include media-breakpoint-up(sm) {}

// 992
@include media-breakpoint-up(md) {
    body.styleguide {
        padding: 0;
    }

    #styleguide {
        .aside {
            position: fixed; top: 0; bottom: 0; left: 0;
            width: 200px; height: 100vh;
            overflow: auto;
            padding: 0;
            background: #eee;
            display: block;

            a {
                transition: padding-left $duration, background $duration;
                padding: 15px;

                &:hover {
                    padding-left: 18px;
                    background: darken(#eee, 10);
                    text-decoration: none;
                }
            }
        }

        .main {
            margin-left: 200px;
            padding: 30px;
        }

        #cards {
            .card { float: left; }
        }
    }
}

// 1200
@include media-breakpoint-up(lg) {}

// 1600
@include media-breakpoint-up(xlg) {}
