/******************************************************************

Stylesheet: Global

******************************************************************/

/*********************
GENERAL
*********************/

* {
	margin: 0; padding: 0;
	box-sizing:border-box;

	text-rendering: optimizeLegibility;
	// -webkit-font-smoothing: antialiased;
	// -moz-osx-font-smoothing: grayscale;
}

html {
	min-height: 100%;
	position: relative;
	overflow-x: hidden;

	&.loading {
		overflow: hidden;

		.main-loader-container {
			opacity: 1;
			visibility: visible;
		}
	}
}

body {
    padding-right: 0!important;
	padding-bottom: 768px; // footer height
	min-width: 320px;
	font-size: $font-size-xs;

	/**
	 * Fix window scroll on mobile
	 */
	&.modal-open {
		overflow: hidden;
    	position:fixed;
	    width: 100%;

		.modal { padding-right: 0!important; }
	}
}

ul {
	margin-bottom: 0;
}

.readmore {
	@include fontSize(12px);
	color: $ofs-danger;

	.icon {
		line-height: 22px;
		margin-right: 5px;
		float: left;
	}
}

.container {
	width: 100%;
}

.main-content {
	h2, h3 {
		@include ofs-subtitle;

		margin-top: 25px;
		margin-bottom: 35px;
	}
}

.main-container {
	padding-top: 70px;
}

.main-container-xs {
	padding-top: 25px;
}

.mark, mark {
    background: none;
    font-weight: $fw-lg;
}

/*********************
 * LAZY LOADING
 ********************/

.lazy {
	opacity: 0;
	transition: opacity $duration * 2;

	&.loaded { opacity: 1; }
}


/*********************
* MEDIA QUERIES
********************/

// 576
@include media-breakpoint-up(xs) {
	body {
		padding-bottom: 517px; // footer height
	}
}

// 768
@include media-breakpoint-up(sm) {
	body {
		padding-bottom: 338px; // footer height
	}

	.main-container {
		padding-top: 90px;
	}

	.main-container-xs {
		padding-top: 45px;
	}
}

// 992
@include media-breakpoint-up(md) {
	body {
		padding-bottom: 276px; // footer height
	}
	
	.main-container {
		padding-top: 120px;
	}

	.container {
		width: 960px;
	}
}

// 1200
@include media-breakpoint-up(lg) {
	.container {
		width: 1080px;
	}
}

// 1600
@include media-breakpoint-up(xlg) {
	.container {
		width: 1280px;
	}

}
