/******************************************************************

Stylesheet: Banners

******************************************************************/

/*********************
 * GLOBAL
 ********************/

.banner {
    background: $ofs-lightgrey;
    @include fullscreen-bg;

    &--legend {
        @include fontSize(12px);
        position: absolute; bottom: 5px; right: 10px;
        color: $ofs-grey;
        margin-bottom: 0;
        opacity: .75;
    }
}

/*********************
 * DEFAULT
 ********************/

.default-banner {
    padding-top: 75px;
    
    .default-banner-inner {
        position: relative;
        background: $ofs-lightgrey;

        width: 100%; height: 0;
        padding-top: 30.75%;

        @include fullscreen-bg;
    }
}

/*********************
 * HOMEPAGE
 ********************/

.homepage-banner {
    background: $ofs-xdarkgrey;
    position: relative;
    // min-height: 385px;
    // min-height: 500px;
    overflow: hidden;

    .banner {
        // position: absolute;
        margin-top: -70px;
        padding: 70px 0 35px;

        &--visual {
            margin: 70px auto 0;
            // width: 70%;
            width: 290px;
            text-align: center;
            
            img {
                width: 100%;
                
            }
        }

        &--inner {
            text-align: center;
            width: 100%;
            padding: 0 15px;
        }

    	h1 {
    		@include animated;
            @include fadeInDownShort;
            @include fontSmoothing;

            color: $white;
            text-align: center;
            font-size: 6vw;
            line-height: 1.2;
            margin-bottom: 0;
        }
        
    	h2 {
    		@include animated;
            @include fadeInDownShort;
            @include fontSmoothing;
			@include delay(250ms);
            @include fontSize(18px);

            color: $white;
            text-align: center;
            margin-top: 25px; margin-bottom: 0;
    	}

        .btn {
			@include animated;
			@include fadeIn;
			@include delay(500ms);

            margin-top: 25px;
        }
    }
}

/*********************
 * MEDIA QUERIES
 ********************/

// 576
// @include media-breakpoint-up(xs) {}

// 768
@include media-breakpoint-up(sm) {
    section.default-banner {
        padding-top: 90px;

        .container {
            background: $ofs-lightgrey;
        }
    }

    section.homepage-banner {
        min-height: 485px;

        .banner {
            h1 {
                @include fontSize(42px);
            }
        }
    }
}

// 992
@include media-breakpoint-up(md) {
    section.default-banner {
        padding-top: 120px;
        
        .default-banner-inner {
            margin: 15px auto;
            max-width: 90%;
        }
    }

    section.homepage-banner {
        .banner {
            margin-top: 0; padding: 0;

            .btn {
                margin-top: 35px;
            }
                
            &--inner {
                position: relative;
                max-width: 1080px;
                margin: 0 auto;

                min-height: 485px;
            }

            &--content {
                position: absolute; left: 30px; top: calc(50% + 35px);
                transform: translateY(-50%);
                text-align: left;
                
                h1,
                h2 {
                    text-align: left;
                }
            }

            &--visual {
                position: absolute; right: 30px; top: calc(50% + 35px);
                transform: translateY(-50%);
                width: 320px;
                margin: 0;
            }
        }

    }

    body.single-events .default-banner-inner {
        margin-top: 0;
        max-width: 100%;
    }
}

// 1200;
@include media-breakpoint-up(lg) {
    section.default-banner {
        .default-banner-inner {
            margin: 15px auto;
            max-width: 1150px;
            padding-top: 30.75%;
        }
    }

    section.homepage-banner {
        .banner--content {
            h1 {
                @include fontSize(45px);
            }
        }
    }

    body.single-events .default-banner-inner {
        margin-top: 0;
        max-width: 100%;
        padding-top: 25%;
    }
}

// 1600;
@include media-breakpoint-up(xlg) {
    section.default-banner {
        .default-banner-inner {
            margin: 25px auto;
            max-width: 1280px;
            padding-top: 25%;
        }
    }

    section.homepage-banner .banner--inner { max-width: 1280px; }

    body.single-events .default-banner-inner {
        margin-top: 0;
        max-width: 100%;
    }
}
