/******************************************************************

Stylesheet: Products

******************************************************************/

/*********************
 * GLOBAL
 ********************/

.product {
    .woocommerce-LoopProduct-link {
        text-align: center;
    }

    .product-title {
        font-weight: $fw-md;
        font-size: 18px;
        color: $ofs-xdarkgrey;
        margin-bottom: 10px;
    }
}

/*********************
 * TOP SELLERS
 ********************/

// section.top-sellers {
