//     }
// }

/*********************
 * RELATED PRODUCTS
 ********************/

section.related.products {
    clear: left;

    .ofs-subtitle {
        margin-bottom: 25px;
    }
}

/*********************
 * WOOCOMMERCE
 ********************/

.woocommerce,
section.top-sellers .woocommerce {
    ul.products li.product {
        width: 100%;

        .price {
            font-size: 14px;
            color: $ofs-xdarkgrey;
            margin-bottom: 0;
        }

        .onsale {
            top: 10px; right: 10px;
            z-index: 0;
        }

        &.outofstock {
            opacity: .5;

            a {
                cursor: not-allowed;
            }
        }

        a > img { border-radius: 2px; }
    }

    .product .onsale {
        position: absolute; top: 5px; left: 5px;
        width: 55px; height: 55px;
        margin: 0; padding: 0;
        line-height: 55px;
        background: $ofs-red;
        display: block;
    }
}

/*********************
 * MEDIA QUERIES
 ********************/

// 576
@include media-breakpoint-up(xs) {
    .woocommerce,
    section.top-sellers .woocommerce {
        ul.products li.product {
            width: calc(50% - 5px);
            float: left;
            // clear: both;
            margin: 0 0 40px;
            text-align: center;
        }
    }

    section.related.products .product .btn {
        width: 100%;
        padding-left: 10px; padding-right: 10px;
    }

    .woocommerce ul.products li.product.product-type-external .product_type_external {
        margin-top: 32px;
    }
    
    body.term-digital.woocommerce ul.products li.product.product-type-external .product_type_external {
        margin-top: 16px;
    }
}

// 768
@include media-breakpoint-up(sm) {
    .woocommerce {
        ul.products li.product { margin: 0; }

        .full-width,
        section.related.products {
            ul.products li.product {
                width: calc(33.333% - 10px);
                float: left;
                clear: none;

                &:nth-child(3n + 1) {
                    margin: 10px 10px 30px 0;
                }

                &:nth-child(3n + 2) {
                    margin: 10px 5px 30px;
                }

                &:nth-child(3n + 3) {
                    margin: 10px 0 30px 10px;
                }
            }
        }

        .has-sidebar {
            ul.products li.product {
                width: calc(50% - 5px);
                margin-top: 0;
                margin-bottom: 40px;

                &:nth-child(odd) {
                    // margin-right: 5px;
                }

                &:nth-child(even) {
                    // margin-left: 5px;
                    float: right;
                }

            }
        }
    }

    section.top-sellers .woocommerce {
        ul.products li.product {
            width: calc(33.333% - 10px);
            float: left;
            clear: none;

            &:nth-child(3n + 1) {
                margin: 10px 10px 30px 0;
            }

            &:nth-child(3n + 2) {
                margin: 10px 5px 30px;
            }

            &:nth-child(3n + 3) {
                margin: 10px 0 30px 10px;
            }
        }
    }
}

// 992
@include media-breakpoint-up(md) {
    .product .product-title { font-size: 15px; }

    .woocommerce .has-sidebar {
        ul.products li.product {
            width: calc(33.333% - 10px);
            float: left;
            clear: none;

            &:nth-child(even) {
                float: left;
            }

            &:nth-child(3n + 1) {
                margin: 10px 10px 30px 0;
                clear: left;
            }

            &:nth-child(3n + 2) {
                margin: 10px 5px 30px;
            }

            &:nth-child(3n + 3) {
                margin: 10px 0 30px 10px;
            }
        }
    }

    section.top-sellers .woocommerce,
    .woocommerce .full-width {
        ul.products li.product {
            width: calc(25% - 10px);

            &:nth-child(4n + 1) {
                margin: 10px 10px 40px 0;
            }

            &:nth-child(4n + 2) {
                margin: 10px 7px 40px 3px;
            }

            &:nth-child(4n + 3) {
                margin: 10px 3px 40px 7px;
            }

            &:nth-child(4n + 4) {
                margin: 10px 0 40px 10px;
            }
        }
    }
}

// 1200
@include media-breakpoint-up(lg) {
    .product .product-title { font-size: 16px; }
}

// 1600
@include media-breakpoint-up(xlg) {
    .woocommerce {
        .has-sidebar {
            ul.products li.product {
                width: calc(25% - 15px);
                float: left;
    
                &:nth-child(4n + 1) {
                    margin: 10px 10px 30px 0;
                    clear: none;
                }
    
                &:nth-child(4n + 2) {
                    margin: 10px 10px 30px;
                    clear: none;
                }
                
                &:nth-child(4n + 3) {
                    margin: 10px 10px 30px;
                    clear: none;
                }

                &:nth-child(4n + 4) {
                    margin: 10px 0 30px 10px;
                    clear: right;
                }
            }
        }

        .full-width {
            ul.products li.product {
                width: calc(20% - 16px);

                &:nth-child(5n + 1) {
                    margin: 10px 10px 50px 0;
                }

                &:nth-child(5n + 2) {
                    margin: 10px 10px 50px;
                }

                &:nth-child(5n + 3) {
                    margin: 10px 10px 50px;
                }

                &:nth-child(5n + 4) {
                    margin: 10px 10px 50px;
                }

                &:nth-child(5n + 5) {
                    margin: 10px 0 50px 10px;
                }
            }
        }

    }
}
