/******************************************************************

Stylesheet: Checkout

******************************************************************/

/*********************
 * GLOBAL
 ********************/

form.checkout {
    h3 {
        margin-bottom: 15px;
    }

    .form-row {
        padding: 0; 

        &.form-row-first, &.form-row-last {
            width: 100%;
        }
    }
}

#customer_details > .col-1 + .col-2 {
    margin-top: 25px;
}

#customer_details > .col-1,
#customer_details > .col-2 {
    max-width: 100%;
    padding: 0;
}

.woocommerce table.shop_table.woocommerce-checkout-review-order-table {
    border-radius: 0;
    width: 100%;
    padding: 15px;
    border: 1px solid #e5e5e5;
}

#order_review_heading {
    margin-top: 25px;
}

.woocommerce-checkout {
    #payment {
        background: none;
         
        .wc_payment_methods {
            display: none;
        }
    
        .form-row {
            padding: 0!important;
        }
    }

    .woocommerce-form-login {
        border: none !important;
        background: $ofs-lightgrey;
        border-radius: 3px !important;
        box-shadow: 0 1px 1px 0 rgba(black, .25);

        .checkbox {
            margin-top: 25px;
        }

        .lost_password a {
            color: $ofs-danger;
        }
    }
}

.woocommerce-account-fields {
    margin-top: 10px;
}

#ship-to-different-address label { margin-bottom: 4px; }
#account_password_field { margin-bottom: 0; }


/*********************
 * MEDIA QUERIES
 ********************/

// 576
@include media-breakpoint-up(xs) {
    form.checkout {
        .form-row {
            &.form-row-first, &.form-row-last {
                width: 49%;
            }
        }
    }
}

// 768
@include media-breakpoint-up(sm) {
    form.checkout {
        .ui.checkbox > label {
            float: right;
        }
    }

    .woocommerce-account-fields label {
        float: none!important;
    }

    #customer_details > .col-1 + .col-2 {
        margin-top: 0;
    }

    .woocommerce-checkout .woocommerce-form-login .form-row {
        width: 48%;
    }
}

// 992
@include media-breakpoint-up(md) {}

// 1200
@include media-breakpoint-up(lg) {}

// 1600
@include media-breakpoint-up(xlg) {}
