/******************************************************************

Stylesheet: Paginations

******************************************************************/

/*********************
 * GLOBAL
 ********************/

.ui.pagination {
	@include fontSmoothing(on);

    ul { margin: 0 auto 25px; }

	li {
        display: inline-block;
		vertical-align: top;
		
		span, a {
            font-weight: $fw-lg;
			font-size: $font-size-xs;
			border-radius: 2px;

			width: 30px; height: 30px;
			line-height: 30px;
            text-align: center;
            display: block;
			color: $ofs-grey;

			transition: background $duration, color $duration, box-shadow $duration;

			&.prev, &.next {
                display: block;
				line-height: 30px;
				
				span {
					font-size: 20px;
				}
			}

			&.disabled {
				border: 2px solid $ofs-grey;
				color: $ofs-grey;

				&:hover {
					cursor: not-allowed;
					background: none;
					color: $ofs-grey;
				}
			}
		}

		a {
			box-shadow: 0 0 0 2px $white inset;
		}

		a:focus, a:hover {
			box-shadow: 0 0 0 2px $ofs-darkgrey inset;
		}

		a:focus, a:hover,
		a:focus span, a:hover span {
			color: $ofs-darkgrey;
			text-decoration: none;
		}
		

        > span {
			background: $ofs-darkgrey;
			color: $white;
			outline: none;
            display: block;
        }

		& + li {
			margin-left: $spacing-sm / 2;
		}
	}
}

/*********************
 * MEDIA QUERIES
 ********************/

// 576
@include media-breakpoint-up(xs) {}

// 768
@include media-breakpoint-up(sm) {}

// 992
@include media-breakpoint-up(md) {}

// 1200
@include media-breakpoint-up(lg) {}

// 1600
@include media-breakpoint-up(xlg) {}
