/******************************************************************

Stylesheet: Single Product

******************************************************************/

/*********************
 * GLOBAL
 ********************/

body.single-product {
    section.title {
        margin-bottom: 20px;
    }

    .woocommerce p.stars a::before { color: #ffe400 !important; }

    .product {
        .summary {
            .stock {
                color: $ofs-darkblue;
            }

            .price {
                color: $ofs-darkblue;
                margin-bottom: 20px;

                span {
                    font-weight: $fw-md;
                }

                > span {
                    font-weight: $fw-md;
                    text-transform: lowercase;

                    &.promo {
                        color: $ofs-red;
                        padding-right: 10px;
                    }
                }

                del {
                    opacity: 1;
                    padding-right: 5px;

                    span {
                        color: $ofs-xdarkgrey;
                    }
                }

                span, ins {
                    text-decoration: none;
                }
            }

            .woocommerce-variation-price {
                // margin-right: 15px;

                &, & + .woocommerce-variation-availability {
                    display: inline-block;
                }

                .price {
                    color: $ofs-red;
                }
            }

            .woocommerce-product-details__short-description {
                p { margin-bottom: 0; }

                margin-bottom: 20px; padding-bottom: 20px;
                border-bottom: 1px solid $ofs-lightgrey;
            }

            .variations_form {
                .label {
                    font-weight: $fw-lg;
                    padding-right: 0!important; margin-right: 10px;
                    width: 70px;
                    line-height: 40px;
                    float: left;
                }

                .value {
                    width: 100%;
                }

                span.select2.select2-container--default {
                    max-width: 122px;
                    margin-right: 15px;
                }

                span.select2 .select2-selection--single {
                    height: 45px;

                    .select2-selection__rendered {
                        line-height: 20px; padding: 0;
                    }

                    .select2-selection__arrow {
                        height: 44px;
                        width: 40px;
                    }
                }

                .reset_variations {
                    font-size: 14px;
                    line-height: 40px;
                    color: $ofs-red;

                    &:hover {
                        color: $ofs-red;
                    }
                }

                .single_variation_wrap .woocommerce-variation,
                .single_variation_wrap  { clear: left; }

                .single_variation_wrap .woocommerce-variation .stock {
                    font-style: italic;

                    &.out-of-stock {
                        color: $ofs-danger;
                    }
                }
            }
        }

        .woocommerce-product-gallery.images > .flex-viewport {
            position: relative;
            padding-top: 125% !important;
            height: 0 !important;

            > figure {
                position: absolute; top: 0;
            }
        }

        .woocommerce-product-gallery.images .flex-control-thumbs {
            margin-top: 5px;

            li + li {
                margin-left: 5px;
            }
        }

        .woocommerce-tabs {
            padding: 15px 0;

            ul.tabs {
                margin: 0; padding: 0;
                margin-bottom: 25px;

                list-style: none;
                text-align: left;
                border-bottom: 1px solid $ofs-lightgrey;

                &::before { display: none!important; }

                li {
                    position: relative;
                    // display: block;
                    margin: 0; padding: 0;
                    background: none; border: none;
                    // border-bottom: 1px solid $ofs-lightgrey;

                    &::before { content: none; }
                    &::after { content: none; }

                    &.active a {
                        background: $ofs-lightgrey;
                        color: $ofs-xdarkgrey;
                        border-radius: $border-radius $border-radius 0 0;
                    }

                    a {
                        padding: 15px;
                        display: block;
                        color: $ofs-grey;
                        @include fontSize(16px);

                        transition: color $duration;

                        &:hover {
                            color: $ofs-xdarkgrey;
                        }
                    }
                }
            }

            .panel h2 { display: none; }
        }
    }
}

/*********************
 * MEDIA QUERIES
 ********************/

// 576
@include media-breakpoint-up(xs) {
    body.single-product {
        .product {
            .summary {
                .single_add_to_cart_button {
                    width: 201px;
                }
            }
        }
    }

}

// 768
@include media-breakpoint-up(sm) {
    body.single-product {
        section.title .ofs-subtitle {
            @include fontSize(22px);
        }

        .product {
            .summary {
                .variations_form {
                    span.select2.select2-container--default {
                        // max-width: 170px;
                        // margin-right: 0;
                    }
                }
            }
        }

        .product .woocommerce-tabs {
            @include clearfix;
            border-top: 1px solid $ofs-lightgrey;

            ul.tabs {
                width: 30%;
                float: left;

                li {
                    display: block!important;

                    &.active a {
                        border-radius: 0;
                    }
                }
            }

            .panel {
                width: calc(70% - 25px);
                float: right;
                margin-right: 0; margin-top: 0;

                h2 {
                    display: block;
                    margin-bottom: 15px;
                }
            }
        }
    }
}

// 992
@include media-breakpoint-up(md) {
    body.single-product {
        section.title .ofs-subtitle {
            @include fontSize(28px);
        }

        .product {
            .summary {
                .variations_form {
                    span.select2.select2-container--default {
                        // max-width: 200px;
                    }
                }
            }
        }
    }

}

// 1200
@include media-breakpoint-up(lg) {}

// 1600
@include media-breakpoint-up(xlg) {}
