/******************************************************************

Stylesheet: Variables

******************************************************************/

/*********************
GRID
*********************/

$enable-flex:           false;

$screen-xs-min : 576px;
$screen-sm-min : 769px;
$screen-md-min : 992px;
$screen-lg-min : 1200px;
$screen-xlg-min : 1600px;

$grid-breakpoints: (
    xxs:0,
    xs: $screen-xs-min,
    sm: $screen-sm-min,
    md: $screen-md-min,
    lg: $screen-lg-min,
    xlg: $screen-xlg-min
);

/*********************
TYPOGRAPHY
*********************/

$font-family-base: 'Montserrat', Helvetica, Arial, sans-serif;
$font-family-secondary: 'Libre Baskerville', Georgia, Times, serif;

/*********************
COLOR
*********************/

$white:              #ffffff;
$black: 	         #000000;

$ofs-lightgrey:      #f5f5f5;
$ofs-grey:           #c1c1c1;
$ofs-darkgrey:       #2f3745;
$ofs-xdarkgrey:      #15191f;

$ofs-lightblue:      #152D6B;
$ofs-blue:           #0b183d;
$ofs-darkblue:       #010624;

$ofs-red:            #fb4040;
$ofs-darkred:        #d41c1c;


// states
$ofs-danger:         $ofs-red;

$ofs-success:        #5cb85c;
$ofs-lightsuccess:   #def5de;
$ofs-darksuccess:    #3c763d;

$ofs-info:           #1e85be;
$ofs-lightinfo:      #dcf2ff;

$ofs-warning:        #ffcd42;

/*********************
ANIMATION
*********************/

$duration : 0.2s;
$easing : ease-in-out;
$easeOutBack : cubic-bezier(0.055, 0.525, 0.435, 1.650);

/*********************
FONTS
*********************/

$fw-xs: 400;
$fw-md: 700;
$fw-lg: 800;

// base 16px
$font-size-xs : .875rem;  // 14
$font-size-sm : 1rem;     // 16
$font-size-md : 1.125rem; // 18
$font-size-lg : 1.5rem;   // 24
$font-size-xl : 1.875rem; // 30

/*********************
SPACING
*********************/

$spacing-xs: 0.9375rem;  // 15
$spacing-sm: 1.25rem;	 // 20
$spacing-md: 1.5625rem;  // 25
$spacing-lg: 1.875rem;   // 30
$spacing-xlg: 2.5rem;	 // 40

/*********************
LOADER
*********************/

$loader-width: 9.375rem;
$loader-border-width: 0.1875rem;

/*********************
BOX SHADOW
*********************/

$box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.25);
$box-shadow-xs: 0 0 1px 0 rgba(0, 0, 0, 0.25);

/*********************
BORDERS
*********************/

$border-radius-lg: 10px;
$border-radius-md: 6px;
$border-radius-xs: 3px;
