

/******************************************************************

Stylesheet: Tables

******************************************************************/

.table,
.woocommerce-MyAccount-orders {
    width: 100%;
    font-size: 14px;

    tbody, tr, td { display: block; }

    th, td { border-top: 0; }

    td {
        padding: .5rem 1rem;
        line-height: 20px;
    }

    thead { display: none; }

    tbody {
        border: solid 1px $ofs-lightgrey;
        // border-radius: 8px;

        tr {
            &:first-child td:first-child { border-radius: 6px 6px 0 0; }
            &:last-child td:last-child { border-radius: 0 0 6px 6px; }

            &:nth-child(odd) td {
                background: $ofs-lightgrey;
            }

            td {
                background-color: $white;
                padding: 5px 15px;

                &:first-child {
                    padding-top: 10px;
                }

                &:last-child {
                    padding-bottom: 10px;
                }

                &.woocommerce-orders-table__cell-order-status {
                    font-weight: $fw-lg;
                }

                &.woocommerce-orders-table__cell-order-actions {
                    padding: 15px 10px 25px;

                    .btn + .btn {
                        margin-top: 5px;
                    }
                }
            }
        }
    }
}

.woocommerce-MyAccount-content {
    .woocommerce-button--next {
        display: block;
        text-align: center;
    }
} 

body.woocommerce-order-received .woocommerce-table--order-details {
    border-collapse: collapse;
    border: none;
    margin: 0;
    border-radius: 0;

    tbody {
        border-radius: 0;
        border: 1px solid #e5e5e5;
    }

    td {
        padding: 10px 15px;
    }

    .woocommerce-table__product-name {
        border-radius: 0!important;
        border-top: none;
    }

    .woocommerce-table__product-total {
        border-radius: 0;
        background: $white;
    }
}

/*********************
 * MEDIA QUERIES
 ********************/

// 576
@include media-breakpoint-up(xs) {
    .woocommerce-MyAccount-content {
        .woocommerce-button--next {
            display: inline-block;
        }
    } 

    .table,
    .woocommerce-MyAccount-orders {
        tbody tr td.woocommerce-orders-table__cell-order-actions {
            .btn + .btn {
                margin-top: 0;
                margin-left: 5px;
            }
        }
    }

}

// 768
@include media-breakpoint-up(sm) {
    .table,
    .woocommerce-MyAccount-orders {
        tr { display: table-row; }

        thead {
            display: table-header-group;
            border-bottom: solid 1px #e1e1e1;

            th {
                border-bottom: none;
                border-top: solid 1px #e1e1e1;
                background-color: $white;
                padding: 10px 15px;

                &:first-child {
                    // border-radius: 5px 0 0 0;
                    border: solid 1px #e1e1e1;
                    border-bottom: none;
                    background-color: $white;
                    display: block;
                }

                &:last-child {
                    // border-radius: 0 5px 0 0;
                    border: solid 1px #e1e1e1;
                    border-bottom: none;
                    border-left: none;
                    display: block;
                    margin-right: -1px;
                }

                & + th {
                    border-right: solid 1px #e1e1e1;
                }
            }
        }

        tbody {
            display: table-row-group;
            border: none;

            tr {
                &:first-child td:first-child,
                &:last-child td:last-child { border-radius: 0; }

                border-left: solid 1px #e1e1e1;
                border-right: solid 1px #e1e1e1;

                td {
                    display: table-cell;
                    border-right: solid 1px #e1e1e1;
                    line-height: 24px;
                    padding: 20px 15px;

                    &:first-child {
                        display: block;
                        padding-top: 20px;
                    }

                    &.woocommerce-orders-table__cell-order-actions {
                        padding: 10px 15px;
                    }
                }

                &:nth-child(odd) th,
                &:nth-child(odd) td {
                    background: $ofs-lightgrey;
                }

                &:last-child {
                    border-left: 0;
                    border-right: 0;

                    th, td:first-child {
                        // border-radius: 0 0 0 5px;
                        border: solid 1px #e1e1e1;
                        border-top: none;
                        display: block;
                        box-shadow: none;
                    }

                    td:last-child {
                        // border-radius: 0 0 5px 0;
                        border: solid 1px #e1e1e1;
                        border-top: none;
                        border-left: none;
                        display: block;
                        margin-right: -1px;
                        box-shadow: none;
                    }

                    td {
                        box-shadow: 0 -1px 0 0 #e1e1e1 inset;
                    }
                }
            }
        }
    }

    .woocommerce-table--order-details {
        thead th,
        tbody tr td,
        tbody tr:last-child td {
            &:first-child, &:last-child {
                display: table-cell;
                margin-right: 0;
            }
        }
    }

    .woocommerce-table--customer-details tbody {
        tr th, tr td {
            display: table-cell;
            padding: 15px;
            border-radius: 0;
        }

        tr:first-child {
            border-top: 1px solid #e1e1e1;

            th {
                border-right: 1px solid #e1e1e1;
            }
        }

        tr:last-child th:first-child,
        tr:last-child td:last-child {
            display: table-cell;
            margin-right: 0;
            border-radius: 0;
        }
    }

    body.woocommerce-order-received .woocommerce-table--order-details {
        .woocommerce-table__product-name {
            border-top: 1px solid #e5e5e5;
            padding: 10px 20px;
        }

        td.woocommerce-table__product-total {
            background: #f6f5f6;
        }
    }
}

