/******************************************************************

Stylesheet: Breadcrumbs

******************************************************************/

/*********************
 * GLOBAL
 ********************/

.ui.breadcrumb {
    background: none;
    border-radius: 0;
    margin-top: 25px; margin-bottom: 15px; padding: 0 0 15px 0;
    border-bottom: 1px solid $ofs-lightgrey;

    .breadcrumb-item {
        color: $ofs-xdarkgrey;
        text-transform: lowercase;

        &:first-child > a {
            max-width: 15px;
            overflow: hidden;
            display: inline-block;

            &::before {
                font-family: 'offsiderz';
                content: "\e901";

                padding-right: 5px;
            }
        }

        a {
            font-weight: $fw-md;
            color: $ofs-xdarkgrey;
            margin-bottom: 0;

            transition: color $duration;

            &:hover {
                color: $ofs-red;
                text-decoration: none;
            }
        }
    }
}

body.single-product .breadcrumb {
    margin-bottom: 25px;
}

/*********************
 * MEDIA QUERIES
 ********************/

// 576
@include media-breakpoint-up(xs) {
    .ui.breadcrumb .breadcrumb-item {
        &:first-child > a {
            max-width: none;
            overflow: auto;
        }
    }

}

// 768
@include media-breakpoint-up(sm) {
    body.single-product .breadcrumb {
        margin-bottom: 35px;
    }
}

// 992
@include media-breakpoint-up(md) {}

// 1200
@include media-breakpoint-up(lg) {}

// 1600
@include media-breakpoint-up(xlg) {}
