/******************************************************************

Stylesheet: Menus

******************************************************************/

/*********************
* GLOBAL
********************/

#main-navigation {
    position: absolute; top: 75px; width: 100%;
    background-color: rgba(21, 25, 31, 1);

    &.show {
        overflow-y: auto;
        max-height: calc(100vh - 75px);
    }
}

.menu {
    ul {
        list-style-type: none;
    }

    li {
        border-bottom: solid 1px #22222f;
    }

    a {
        @include fontSmoothing(on);
        color: $white;
        font-size: $font-size-lg;
        font-weight: $fw-md;
        padding: 17px 25px;
        display: inline-block;
    }

    .has-submenu {
        position: relative;
        background: $ofs-lightgrey;

        > a {
            display: block;
            background-color: $ofs-xdarkgrey;
        }

        .accordion-btn {
            position: absolute; top: 0; right: 0;
            height: 40px; width: 40px;
            padding: 0; margin: 15px;

            .icon {
                color: $ofs-lightgrey;
                font-size: 25px;
                display: block;
                transition: transform $duration;
                transform: rotate(0);
            }

            &.collapsed .icon {
                transform: rotate(-45deg);
            }
        }
    }

    .submenu-item {
        box-shadow: 0 0 3px 0 rgba($black, .2);

        ul { padding: 5px 0; }

        li { border-bottom: none; }

        a {
            @include fontSmoothing(off);
            display: block;
            color: $ofs-xdarkgrey;
            padding: 5px 22px 5px 25px;
            font-size: $font-size-sm;
            transition: color $duration;

            .icon {
                font-size: 25px;
                float: right;
                transition: color $duration;
            }

            &.active, &:hover {
                color: $ofs-red;

                .icon {
                    color: $ofs-red;
                }
            }

        }
    }

    &.menu-secondary {
        li:last-child { display: none; }
    }
}

/*********************
 * MEDIA QUERIES
 ********************/

// 576
@include media-breakpoint-up(xs) {}

// 768
@include media-breakpoint-up(sm) {
    #main-navigation {
        top: 90px;
    }
}

// 992
@include media-breakpoint-up(md) {
    #main-navigation {
        display: block;
        background: none;
        top: 0;
        height: 90px;

        &.show {
            overflow-y: visible;
        }
    }

    .menu {
        line-height: 100px;
        padding: 0;
        width: 50%;

        ul { @include clearfix; }

        li {
            border: none;
            position: relative;
            float: left;

            &.active a::before {
                transform: scaleX(1);
                opacity: 1;
            }
        }

        a {
            font-size: $font-size-xs;
            padding: 10px 20px;

            &::before {
                position: absolute; top: 0;
                width: calc(100% - 40px); height: 6px;
                background-color: $ofs-red;
                transform: scaleX(0);
                opacity: 0;
                transition: transform $duration, opacity $duration;
                content: '';
            }

            &:hover::before {
                transform: scaleX(1);
                opacity: 1;
            }
        }

        &.menu-primary {
            float: left;
            padding-right: 120px;

            ul { float: right; }
        }

        &.menu-secondary {
            float: right;
            padding-left: 100px;

            ul {
                float: left;
                padding-left: 0;
            }

            li:last-child { display: block; }
        }

        .has-submenu {
            background: transparent;

            > a {
                background: transparent;
            }

            .accordion-btn { display: none; }
            .collapse { display: block; }


            &:hover {
                a::before {
                    transform: scaleX(1);
                    opacity: 1;
                }

                .submenu-item {
                    top: calc(50% + 35px);
                    visibility: visible;
                    opacity: 1;
                }
            }
        }

        .submenu-item {
            position: absolute; top: calc(50% + 50px); left: -10%;
            background: $ofs-lightgrey;

            width: 130%;
            padding: 15px;
            border-radius: 5px;

			visibility: hidden;
			opacity: 0;

			transition: opacity $duration, top $duration, visibility $duration;

            &::before {
                position: absolute; top: -7px; left: calc(50% - 10px);
                width: 0; height: 0;
                border-style: solid;
                border-width: 0 10px 7px;
                border-color: transparent transparent $ofs-lightgrey;
                content: "";
            }

            ul {
                padding: 0;
                float: none;

                li {
                    line-height: 15px;
                    float: none;

                    a {
                        padding: 0; margin: 0 0 10px 0;
                        font-weight: $fw-xs;
                        font-size: 12px;
                        color: $ofs-xdarkgrey;

                        transition: padding-left $duration;

                        &::before { display: none; }

                        &:hover {
                            padding-left: 2px;
                        }

                        .icon { display: none; }
                    }

                    &:last-child a { margin-bottom: 0; }
                }
            }

        }
    }
}

// 1200
@include media-breakpoint-up(lg) {
    .menu {
        a {
            font-size: $font-size-sm;
            padding: 10px 25px;

            &::before {
                width: calc(100% - 50px);
            }
        }

        &.menu-primary {
            padding-right: 150px;
        }

        &.menu-secondary {
            padding-left: 130px;
        }
    }
}

// 1600
@include media-breakpoint-up(xlg) {
    .menu {
        a {
            padding: 10px 50px;

            &::before {
                width: calc(100% - 100px);
            }
        }

        &.menu-primary {
            padding-right: 150px;
        }

        &.menu-secondary {
            padding-left: 130px;
        }

        .submenu-item {
            width: 100%;
            left: 0;
        }
    }
}
