/******************************************************************

Stylesheet: Search

******************************************************************/

/*********************
GLOBAL
*********************/

body.search {
    .page-header {
        margin-bottom: 25px;
    }

    .main-container {
        padding-bottom: 40px;
    }

    &.search-results article {
        & + article { margin-top: 40px; }

        .visual {
            width: 100%;
            margin-bottom: 15px;

            a {
                display: block;

                img {
                    @include img-fluid
                    border-radius: 2px;
                }
            }
        }

        .content {
            text-align: center;

            .price {
                font-size: 14px;
                color: #15191f;
                margin-bottom: 0;
                display: block;
            }

            span.btn {
                margin: 15px 0;
            }
        }
    }
}

/*********************
OVERLAY
*********************/

.search-ovelay {
    background: $white;

    > .trigger-search-overlay {
        display: none;
    }
}

.search-form {
    form {
        padding: 0;
        margin: 0 auto;
    }

    input {
        font-size: $font-size-md;
        font-style: italic;
        color: $ofs-darkgrey;
        background: none;

        padding: $spacing-md $spacing-sm $spacing-md 0;
        height: 60px;

        border-radius: 0;
        border: none;

        outline: none;
        box-shadow: none! important;

        &:focus {
            background: none;
            outline: none;
            box-shadow: none! important;
        }
    }

    .input-group {
        width: 100%;
    }

    .input-group-addon {
        text-align: right;
        border: none;
        background: none;
        padding: 0;
    }

    .icon {
        font-size: 20px;
        color: $ofs-darkgrey;
    }
}

/*********************
 * MEDIA QUERIES
 ********************/

// 576
@include media-breakpoint-up(xs) {
    body.search.search-results article {
        & + article { margin-top: 0; }

        width: calc(50% - 5px);
        float: left;
        margin: 0 0 40px;
        text-align: center;

        &:nth-child(2n + 1) {
            float: right;
        }

    }
}

// 768
@include media-breakpoint-up(sm) {
    body.search.search-results {
        .results-container {
            margin: 0 -10px;
        }

        article {
            width: calc(33.333%);
            float: left !important;
            clear: none;
            padding: 10px;
        }
    }
}

// 992
@include media-breakpoint-up(md) {
    .search-ovelay {
        display: block;

        position: fixed; top: 0; left: 0;
        width: 100%; height: 100%;
        padding: 100px 0;

        background-color: rgba(21, 25, 31, 0.95);
        z-index: 10000;

    	opacity: 0;
    	visibility: hidden;
    	transition: opacity 0.5s, visibility 0s 0.5s;

        &.open {
        	opacity: 1;
        	visibility: visible;
        	transition: opacity 0.5s;

            form .input-group {
               opacity: 1;
               transform: rotateX(0deg);
            }
        }

        &.closing {
            form .input-group  {
            	transform: translateY(25%) rotateX(-35deg);
            }
        }

        .trigger-search-overlay {
            position: absolute; top: 10px; right: 20px;
            font-size: $font-size-xl;
            color: $white;
            display: block;
        }
    }

    .search-form {
        form {
            transform: translateY(-50%);
            perspective: 1200px;
            width: 90%;

            .input-group {
                opacity: 0.4;
                transform: translateY(-25%) rotateX(35deg);
                transition: transform 0.5s, opacity 0.5s;
            }
        }

        input {
            font-size: $font-size-xl;
            font-style: normal;
            color: $white;

            height: 75px;
            border-bottom: solid 1px $ofs-darkgrey;

            &:focus {
                color: $white;
                border-bottom: solid 1px $ofs-darkgrey;
            }
        }

        .icon {
            color: $white;
        }

        .cancel-btn {
            display: block;
        }
    }

}

// 1200
@include media-breakpoint-up(lg) {
    body.search.search-results article {
        width: calc(25%);
    }
}

// 1600
@include media-breakpoint-up(xlg) {}
