/******************************************************************

Stylesheet: Asides

******************************************************************/

/*********************
 * GLOBAL
 ********************/

aside.sidebar {
    h3 {
        border-bottom: .25rem solid $ofs-darkblue;
        padding-bottom: 13px; margin-bottom: 20px;
        text-transform: lowercase;
    }

    .widget + .widget { margin-top: 30px; }

    .events {
        ul { margin-bottom: 25px; }
        
        li + li {
            margin-top: 15px;
        }
        
        a {
            @include clearfix;
        }

        .calendar {
            float: left;
            margin-right: 10px;
        }

        .content {
            width: calc(100% - 75px);
            float: left;

            .title {
                font-weight: $fw-md;
            }

            p {
                margin-bottom: 0;
                color: $ofs-darkblue;
            }
        }
    }
}

/*********************
 * WOOCOMMERCE
 ********************/

.woocommerce .widget {
    .product-categories {
        margin-left: 16px;

        li + li {
            margin-top: 5px;
        }

        .current-cat a { font-weight: $fw-md; }

        a {
            color: $ofs-darkblue;
            transition: color $duration;

            &:hover {
                text-decoration: none;
                color: $ofs-red;
            }
        }
    }

    .woocommerce-widget-layered-nav-list {
        li {
            position: relative;
        }

        li + li {
            margin-top: 10px;
        }

        a {
            color: $ofs-darkblue;
            transition: color $duration;
            padding-left: 35px;

            &:hover {
                text-decoration: none;
                color: $ofs-red;
            }

            &::before {
                position: absolute; left: 0; top: calc(50% - 14px);
                width: 23px; height: 23px;
                
                background: #fff;
                border: 1px solid #c6c6c6;

                border-radius: .1875rem;
                box-shadow: 0 1px 2px 0 rgba(0,0,0,.25);
                display: block;
                float: left;
                margin-right: .75rem;
                content: '';
            }
        }

        .woocommerce-widget-layered-nav-list__item--chosen {
            a::after {    
                position: absolute; top: -3px; left: 1px;
                font-family: offsiderz;
                font-size: 22px;
                line-height: 22px;
                color: #2f3745;
                text-align: center;
                content: "\e907";
            }
        }
    }

    .widget_shopping_cart_content {
        .woocommerce-mini-cart__total {
            border-top: 1px solid $ofs-lightgrey;
            text-align: center;
            padding-top: 10px; margin-top: 10px;
        }

        .woocommerce-mini-cart__buttons {
            .btn { display: block; }
        }

        .cart_list li.woocommerce-mini-cart-item {
            padding-left: 0; padding-bottom: 0;

            & + .woocommerce-mini-cart-item { padding-top: 15px; }

            a {
                font-size: 12px; line-height: 14px;
                color: $ofs-darkblue;

                &.remove {
                    position: relative; left: auto; top: auto;
                    font-size: 12px;
                    line-height: 19px;

                    padding-left: 15px;
                    font-weight: $fw-xs;
                    color: $ofs-danger!important;

                    display: block;
                    height: auto; width: auto;
                    text-align: left;

                    &:hover {
                        color: $ofs-danger!important;
                        background: none;
                        text-decoration: underline;

                        .icon {
                            background: none;
                        }
                    }

                    .icon {
                        position: relative; left: 0;
                        font-size: 18px; line-height: 18px;

                        padding: 0px 4px 0 0;
                        float: left;
                        display: block;
                    }
                }
            }

            img {
                width: 40px;
                margin-left: 0; margin-right: 10px;
                float: left;
            }
        }
    }
}

/*********************
 * MEDIA QUERIES
 ********************/

// 576
@include media-breakpoint-up(xs) {}

// 768
@include media-breakpoint-up(sm) {
    aside.sidebar {
        .widget_shopping_cart { margin-top: 29px; }
    }

    body.single-product {
        .widget_shopping_cart { margin-top: 0; }
    }
}

// 992
@include media-breakpoint-up(md) {}

// 1200
@include media-breakpoint-up(lg) {}

// 1600
@include media-breakpoint-up(xlg) {}
