/******************************************************************

Stylesheet: Cards

******************************************************************/

/*********************
 * GLOBAL
 ********************/

.ui.card {
    border-radius: $border-radius-xs;
    box-shadow: $box-shadow-xs;
    border: none;

    & + .card {
        margin-top: 15px;
    }
}

.card-visual {
    position: relative;
    border-radius: $border-radius-xs $border-radius-xs 0 0;
    overflow: hidden;

    @include ratio(65%);
    
    background: $ofs-lightgrey;
    
    .lazy {
        @include fullscreen-bg;
        @include ratio(65%);

        position: absolute; top: 0; left: 0;
    }
}

.card-block {
    .date {
        font-weight: $fw-md;
        color: $ofs-danger;
        margin: 0 0 15px 0; 
    }
}

