/******************************************************************

Stylesheet: Buttons

******************************************************************/

/*********************
MIXINS
*********************/

@mixin boxsadow($color) {
    box-shadow: 0px -2px 0px 0px darken($color, 25) inset;
}

/*********************
GENERAL
*********************/

.btn,
.woocommerce-Button,
.woocommerce a.button,
.woocommerce a.button.alt {
    @include fontSmoothing(on);
    padding: 12px 30px 14px;
    width: 100%;

    font: $fw-md 11px $font-family-base;
    letter-spacing: 1px;
    text-transform: uppercase;
    text-align: center;

    border: none;
    border-radius: 0.1875rem;

    color: white;
    background: $ofs-red;
    @include boxsadow($ofs-red);

    transition: background-color $duration;

    span {
        display: block;
        pointer-events: none;
    }

    &:hover {
        color: $white;
        background: darken($ofs-red, 10);
    }

    &:focus {
        color: $white;
        outline: none;
        border: none;
        @include boxsadow($ofs-red);
    }

    &:active {
        box-shadow: none!important;
        padding-bottom: 12px;

        span {
            padding-top: 2px;
        }
    }

    &.btn-secondary {
        background: $ofs-darkgrey;
        @include boxsadow($ofs-darkgrey);

        &:hover {
            background: darken($ofs-darkgrey, 10);
        }
    }

    &, &-loader {
        position: relative;
        transition: padding-right $duration;
    
        &::after {
            @include fontSmoothing(off);
            position: absolute; right: 20px; top: calc(50% - 10px);
            font-size: 20px; line-height: 20px;
            height: 20px; width: 20px;
            text-align: center;
            transform-origin: 50%;
    
            opacity: 0;
    
            transition: opacity $duration;
    
            font-family: 'offsiderz' !important;
            content: '\e90b' !important;
        }
    
        &.loading {
            padding-right: 50px;
    
            &::after {
                animation: spin 1.5s infinite linear;
                top: calc(50% - 10px) !important;
                opacity: 1;
            }
        }
    }
}

/*********************
 * WOOCOMMERCE
 ********************/

.woocommerce-Button {
    background: $ofs-darkgrey;
    @include boxsadow($ofs-darkgrey);

    &:hover {
        background: darken($ofs-darkgrey, 10);
    }

    &:active, &:focus {
        @include boxsadow($ofs-darkgrey);
    }
}

.woocommerce a.button,
.woocommerce a.button.alt {
    margin-top: 15px;
    margin-bottom: 10px;

    &.loading, &.added {
        padding-right: 40px;

        &::after {
            position: absolute; top: calc(50% - 6px); right: 15px;
        }
    }

    &.loading::after {
        top: 18px; right: 10px;
    }

    &.added + .added_to_cart {
        font-size: 11px;
        font-weight: $fw-md;
        text-transform: uppercase;
        color: $ofs-darkgrey;
        padding: 0;
        display: block;

        transition: color $duration;

        &:hover {
            color: $black;
        }
    }
}

/*********************
 * MEDIA QUERIES
 ********************/

// 576
@include media-breakpoint-up(xs) {
    .btn,
    .woocommerce a.button,
    .woocommerce a.button.alt,
    .woocommerce-Button {
        width: auto;
    }

    .woocommerce a.button.added + .added_to_cart {
        position: absolute;
        width: 100%;
        text-align: center;
    }
}

// 768
@include media-breakpoint-up(sm) {}

// 992
@include media-breakpoint-up(md) {}

// 1200
@include media-breakpoint-up(lg) {}

// 1600
@include media-breakpoint-up(xlg) {}
