/******************************************************************

Stylesheet: Header

******************************************************************/

/*********************
 * GLOBAL
 ********************/

header.main-header {
    position: fixed;
    width: 100%; height: 75px;
    z-index: 10000; // fix cart loading depth issue
    transition: background $duration;
    background: $ofs-xdarkgrey;

    .ui.logo {
        position: absolute; top: 10px; left: calc(50% - 60px);
        max-width: 120px;
        display: block;
        z-index: 1;

        img {
            @include img-fluid;
        }
    }
}

body.home header.main-header {
    background: transparent;
}

body.scrolled header.main-header {
    background: $ofs-xdarkgrey;
}

/*********************
 * MEDIA QUERIES
 ********************/

// 576
@include media-breakpoint-up(xs) {}

// 768
@include media-breakpoint-up(sm) {
    header.main-header {
        height: 90px;

        .ui.logo {
            top: 15px; left: calc(50% - 65px);
            max-width: 130px;
        }
    }
}

// 992
@include media-breakpoint-up(md) {
    header.main-header {
        height: 120px;

        .ui.logo {
            top: 15px; left: calc(50% - 100px);
            max-width: 200px;
        }
    }
}

// 1200
@include media-breakpoint-up(lg) {}

// 1600
@include media-breakpoint-up(xlg) {}
