/******************************************************************

Stylesheet: Cart

******************************************************************/

/*********************
 * CART
 ********************/

body.panier .woocommerce-cart-form {
    table { border-radius: 0; }

    thead {
        background: $ofs-lightgrey;

        th {
            padding: 15px;
        }
    }

    th.product-thumbnail { width: 120px; }

    td.product-thumbnail {
        padding: 0;

        img {
            width: 120px;
        }
    }

    td.product-name a {
        @include fontSize(16px);
        color: $ofs-xdarkgrey;
        transition: color $duration;

        &:hover, &:focus {
            text-decoration: none;
            color: $ofs-red;
        }
    }

    .woocommerce-Price-amount {
        font-weight: $fw-lg;
    }

    .product-subtotal .woocommerce-Price-amount { color: $ofs-red; }

    .product-remove > a {
        margin: 0 auto;
        border: 1px solid $ofs-grey;
        border-radius: $border-radius-xs;
        // border-radius: 0;

        width: 35px; height: 35px;
        line-height: 35px;
        font-size: 15px;
        color: $ofs-grey;
        background: $white;

        transition: border $duration;

        .icon {
            color: $ofs-darkgrey;

            // transition: color $duration;
        }

        &:hover {
            border: 1px solid $ofs-darkgrey;
            // border: $ofs-darkgrey;
            //
            // .icon { color: $white; }
        }
    }

    tr > .actions {
        background: $ofs-lightgrey;
        padding: 15px;
    }

    .woocommerce-cart-form__contents {
        td {
            padding: 15px;
            text-align: left !important;

            &::before {
                width: 100px;
                margin-right: 15px;
            }

            &.product-quantity::before {
                line-height: 40px;
            }

            &.product-remove {
                &::before {
                    content: attr(data-title) ": " !important;
                    display: block!important;
                    line-height: 35px;
                }

                a {
                    margin-left: 115px;
                }
            }
        }
    }
}

/*********************
 * TOTALS
 ********************/

.cart-collaterals .cart_totals {
    // margin-top: 30px;
    border: 1px solid #e5e5e5;
    padding: 25px;

    .ofs-subtitle {
        margin-bottom: 25px;
    }

    table.shop_table {
        border: none;

        tr > th { padding-left: 0; }
    }

    .wc-proceed-to-checkout {
        @include clearfix;
        padding-bottom: 0;

        .btn {
            float: right;
        }
    }
}


/*********************
 * MEDIA QUERIES
 ********************/

// 576
@include media-breakpoint-up(xs) {
    body.panier .woocommerce-cart-form {
        .woocommerce-cart-form__contents td.actions { text-align: right !important; }
    }
}

// 768
@include media-breakpoint-up(sm) {
    .cart-collaterals .cart_totals {
        max-width: 500px;
        margin-top: 30px;
    }

    body.panier .woocommerce-cart-form {
        .woocommerce-cart-form__contents {
            td.product-remove {
                &::before { display: none!important; }

                a {
                    margin-left: auto;
                }
            }
        }
    }
}

