/******************************************************************

Stylesheet: Layout

******************************************************************/

/*********************
 * GLOBAL
 ********************/

.main-container > .woocommerce {
    @include clearfix;

    margin-bottom: 50px;
}

/*********************
 * WRAPPERS
 ********************/

.section-wrapper {
    @include clearfix;
    text-align: center;

    .ofs-subtitle {
        margin-bottom: 25px;
    }
}

/*********************
 * MEDIA QUERIES
 ********************/

// 576
@include media-breakpoint-up(xs) {}

// 768
@include media-breakpoint-up(sm) {
    .has-sidebar {
        @include clearfix;

        .main {
            width: 70%;
            float: left;
        }

        .sidebar {
            width: calc(30% - 15px);
            margin-left: 15px;
            float: left;
        }
    }
}

// 992
@include media-breakpoint-up(md) {
    .has-sidebar {
        .main {
            width: calc(100% - 250px);
        }

        .sidebar {
            margin-left: 30px;
            width: 220px;
        }
    }

    .section-wrapper {
        .ofs-subtitle {
            margin-bottom: 45px;
        }
    }
}

// 1200
@include media-breakpoint-up(lg) {}

// 1600
@include media-breakpoint-up(xlg) {}
