section.map-container {
    padding-top: 70px;
    background: $ofs-lightgrey;
}

#map {
    position: relative;
}

.mapboxgl-canvas-container {
    position: absolute; top: 0;
}

.mapboxgl-canvas {
    outline: 0;
}

