/******************************************************************

Stylesheet: Alerts

******************************************************************/

/*********************
 * GLOBAL
 ********************/

.ui.alert {
    position: relative;
    border-radius: $border-radius-xs;
    border: none;
    
    p { margin-bottom: 0; }

    &::before {
        font-family: 'offsiderz';
    }

    &.alert-danger {
        color: $ofs-red;

        .icon-round {
            background: $ofs-red;
        }
    }

    &.alert-success {
        color: $ofs-success;

        .icon-round {
            background: $ofs-success;
        }
    }

    &.has-icon {
        padding-left: 45px;
    }

    .icon {
        position: absolute; top: calc(50% - 12px); left: 15px;
    }
}

/*********************
 * WOOCOMMERCE
 ********************/

@mixin woo-alert {
    position: relative;
    border-radius: $border-radius-xs;
    padding: .75rem 1.25rem;
    border: none;

    &::before {
        position: absolute; top: calc(50% - 12px); left: 15px;

        font-family: 'offsiderz';
        font-size: 24px;
        line-height: 24px;
    }

    a {
        color: $ofs-darkblue;
    }
}

.woocommerce-info {
    @include woo-alert;
    padding-left: 45px;

    background: $ofs-lightinfo;
    color: $ofs-info;

    &::before {
        content: "\e904";
    }
}

.woocommerce-message {
    @include woo-alert;
    padding-left: 45px;

    background: $ofs-lightsuccess;
    color: $ofs-darksuccess;

    &::before {
        color: $ofs-success;
        content: "\e905";
    }

    .button {
        @include reset-btn;
    }
}

.woocommerce-error {
    @include woo-alert;
    padding-left: 45px;

    list-style-type: none;
    background: #f2dede;
    color: $ofs-red;

    &::before {
        color: $ofs-danger;
        content: "\e903";
    }

    & + #customer_login {
        margin-top: 15px;
    }

    .button {
        @include reset-btn;
    }
}

/*********************
 * MEDIA QUERIES
 ********************/

// 576
@include media-breakpoint-up(xs) {}

// 768
@include media-breakpoint-up(sm) {
    .woocommerce-error {
        & + #customer_login {
            margin-top: 20px;
        }
    }
}

// 992
@include media-breakpoint-up(md) {}

// 1200
@include media-breakpoint-up(lg) {}

// 1600
@include media-breakpoint-up(xlg) {}
