/******************************************************************

Stylesheet: Footer

******************************************************************/

/*********************
 * GLOBAL
 ********************/

footer.main-footer {
    position: absolute; bottom: 0;
    width: 100%;

    background: $ofs-darkgrey;
    padding: 25px 15px;

    .ui.logo {
        max-width: 150px;
        display: block;
        margin-bottom: 5px;

        img {
            @include img-fluid;
        }

        & + span {
            @include fontSmoothing(on);
        }
    }

    .section {
        h6 {
            @include fontSmoothing(on);
            color: $white;
            text-transform: uppercase;
            font-weight: $fw-md;
            margin-bottom: 10px;
        }

        a {
            color: $ofs-grey;

            transition: color duration;

            & + a {
                margin-top: 10px;
            }

            &:hover, &:focus {
                color: $white;
            }
        }

        ul {
            margin-left: 15px;
            color: $ofs-grey;
            
            li.active a {
                @include fontSmoothing(on);

                font-weight: $fw-md;
                color: $white;
            }

            &.socials {
                margin-left: 0;

                li {
                    display: inline-block;
                    vertical-align: top;
                    width: 25px; height: 25px;
                }
            }
        }

        &:first-child {
            span {
                font-weight: $fw-md;
                font-size: 12px;
                color: $ofs-grey;
            }
        }

        & + .section {
            margin-top: 35px;
        }
    }
}

/*********************
 * MEDIA QUERIES
 ********************/

// 576
@include media-breakpoint-up(xs) {
    footer.main-footer {
        @include clearfix;

        .section {
            width: 50%;
            float: left;
            min-height: 150px;

            &:first-child {
                width: 100%;
                float: none;
                min-height: auto;
            }
        }
    }
}

// 768
@include media-breakpoint-up(sm) {
    footer.main-footer {
        .section {
            width: 25%;
        }
    }
}

// 992
@include media-breakpoint-up(md) {
    footer.main-footer {
        padding: 60px 0;

        .ui.logo {
            max-width: 150px;
        }

        .section {
            width: calc(20% - 10px);

            &:first-child {
                width: calc(20% + 15px);
                float: left;
            }

            & + .section {
                margin-top: 0;
            }
        }
    }
}

// 1200
@include media-breakpoint-up(lg) {

}

// 1600
@include media-breakpoint-up(xlg) {}
