
/******************************************************************

Stylesheet: Socials

******************************************************************/

/*********************
 * GLOBAL
 ********************/

.instagram {
    background: $ofs-xdarkgrey;
    color: $white;
    padding: 35px 0;
    
    .ofs-lead, .ofs-lead a {
        @include fontSmoothing;
        margin-bottom: 25px;

        &, a {
            color: $white;
        }
    }

    &--post a {
        @include fullscreen-bg;
        border-radius: $border-radius-xs;
    }
}

.no-touchevents .slider {
    .instagram--post {
        position: relative;
        display: inline-block;
        width: 100%; height: 0;
        padding-top: 100%;

        a {
            position: absolute; top: 0; left: 0; bottom: 0; right: 0;
            display: block;
        }

        // img {
        //     width: 100%; height: 100%;
        //     display: block;
        //     border-radius: $border-radius-xs;
        // }
    }
}

.touchevents {
    .instagram { margin-bottom: -1px; }

    .slider {
       width: 100%;
       display: flex;
       overflow-x: auto; overflow-y: hidden;
       -webkit-overflow-scrolling: touch;
   
       .instagram--post {
           width: 250px; height: 250px;
           display: inline-block;
   
           & + .slider-item {
               margin-left: 15px;
           }
   
           a {
               width: 250px; height: 250px;
               display: inline-block;
           }
       }
   }
}

/*********************
 * WOOCOMMERCE
 ********************/

/*********************
 * MEDIA QUERIES
 ********************/

// 576
@include media-breakpoint-up(xs) {}

// 768
@include media-breakpoint-up(sm) {
    .instagram {
        padding-top: 40px;
        padding-bottom: 65px;
        
        .ofs-lead {
            margin-bottom: 40px;
        }
    }
}

// 992
@include media-breakpoint-up(md) {}

// 1200
@include media-breakpoint-up(lg) {}

// 1600
@include media-breakpoint-up(xlg) {
    .instagram {
        padding-top: 60px;
        padding-bottom: 85px;
        
        .ofs-lead {
            margin-bottom: 60px;
        }
    }
}
