/******************************************************************

Stylesheet: Single

******************************************************************/

/*********************
 * BIO
 ********************/

body.biographie {
    .main-container-xs {
        margin-bottom: 45px;
    }

    section.title {
        text-align: center;
        margin: 15px auto;

        .ofs-subtitle {
            font-weight: $fw-xs;
        }
    }
}


/*********************
 * MEDIA QUERIES
 ********************/

// 576
// @include media-breakpoint-up(xs) {}

// 768
@include media-breakpoint-up(sm) {
    .single-tpl {
        .main-content {
            max-width: 450px;
            margin: 0 auto;
        }
    }
    
    body.biographie {
        .main-container-xs {
            margin-bottom: 65px;
        }

        section.title {
            margin: 0 auto;
        }
    }
}

// 992
@include media-breakpoint-up(md) {
    body.biographie {
        section.title {
            margin-bottom: 45px;
        }

        .main-content {
            max-width: 100%;
            column-count: 3;

            p {
                margin-left: 10px;
                margin-right: 10px;
            }
        }
    }
}

// 1200
@include media-breakpoint-up(lg) {}

// 1600
@include media-breakpoint-up(xlg) {}
