/******************************************************************

Stylesheet: Pages

******************************************************************/

/*********************
 * CART
 ********************/

body.woocommerce-page {
    .woocommerce {
        margin-top: 15px;
    }

    .return-to-shop {
        margin-bottom: 45px;

        .button {
            margin: 0;
        }
    }
}

body.livraison,
body.mentions-legales,
body.conditions-generales,
body.paiement-securise {
    .main-container {
        margin-bottom: 65px;
    }
}

/*********************
 * LOGIN
 ********************/

#customer_login {
    > div {
        max-width: 100%;
        width: 100%;
        border-radius: $border-radius-xs;
        background: $ofs-lightgrey;
        padding: 15px;

        & + div { margin-top: 15px; }

        h2 {
            margin-bottom: 15px;
        }
    }

    .woocommerce-LostPassword {
        margin-bottom: 0;

        a { color: $ofs-red; }
    }
}


/*********************
 * MEDIA QUERIES
 ********************/

// 576
@include media-breakpoint-up(xs) {
    #customer_login {
        > div { padding: 25px; }
    }
}

// 768
@include media-breakpoint-up(sm) {
    #customer_login {
        @include clearfix;

        > div {
            width: calc(50% - 10px);
            float: left;
            min-height: 405px;

            & + div {
                margin-top: 0;
                margin-left: 20px;
            }
        }
    }
}

// 992
@include media-breakpoint-up(md) {
    #customer_login {
        > div {
            min-height: 425px;
            padding: 35px;
        }
    }
}

// 1200
@include media-breakpoint-up(lg) {}

// 1600
@include media-breakpoint-up(xlg) {}
