/******************************************************************

Stylesheet: Tags

******************************************************************/

/*********************
 * GLOBAL
 ********************/

.ui.tag {
    background: $ofs-lightgrey;
    color: $ofs-xdarkgrey;
    display: inline-block;

    padding: 3px 12px;
    border-radius: 50px;

    &.danger {
        background: $ofs-red;
        color: $white;
    }

    &.success {
        background: $ofs-success;
        color: $white;
    }

    &.info {
        background: $ofs-info;
        color: $white;

    }

    &.warning {
        background: $ofs-warning;
        color: $white;
    }
}

/*********************
 * MEDIA QUERIES
 ********************/

