/******************************************************************

Stylesheet: Mixins

******************************************************************/

/*********************
 * CENTER
 ********************/

@mixin absolute-center {
    position: absolute; top: 50%; left: 50%;
    transform-origin: 50% 50%;
    transform: translate(-50%, -50%);
}

/*********************
 * TITLES
 *********************/

@mixin ofs-title {
    font-weight: $fw-lg;
    clear: both;
}

@mixin ofs-subtitle {
    position: relative;
    padding-bottom: .5em; margin-bottom: .5em;
    display: inline-block;
    clear: both;
    color: $ofs-darkblue;

    &::after {
        position: absolute; bottom: 0; left: 0;
        height: 0.25rem; width: 1em;
        background-color: $ofs-red;
        content: '';
    }
}

@mixin ofs-subtitle-secondary {
    @include ofs-subtitle;

    &::after { background-color: $ofs-xdarkgrey;  }
}

@mixin ofs-lead {
    font-family: $font-family-secondary;
    font-weight: $fw-xs;
}

/*********************
 * FULLSCREEN
 ********************/

@mixin fullscreen-bg {
    background-size: cover;
    background-position: 50%;
    transform-origin: 50%;
    width: 100%; height: 100%;
}

/*********************
* LOADER
********************/

@mixin loader {
    font-family: 'offsiderz';
    content: "\e90b";
    color: $ofs-darkgrey;
    animation: spin 1s infinite linear;
    transform-origin: 50%;

    width: 30px; height: 30px;
    font-size: 30px;
    line-height: 30px;

    display: block;
    text-align: center;
    opacity: .5;
}

/*********************
 * CANCELS
 ********************/

@mixin cancel-list {
    list-style: none;
}

@mixin cancel-link {
    &:hover, &:focus {
        text-decoration: none;
    }
}

@mixin cancel-btn {
    border: none;
    background: none;
    outline: none;
}

@mixin reset-btn {
    background: none!important;
    color: $ofs-darkblue!important;
    margin: 0!important; padding: 0!important;
    box-shadow: none!important;
    text-transform: none!important;
    font-size: 14px!important;
    line-height: 20px!important;
    font-weight: $fw-md!important;
}

/*********************
 * FONTS
 *********************/

@function calculateLineHeight($size) {
    @return $size + 6px;
}

@mixin fontSize($font-size) {
    font-size: $font-size;
    line-height: calculateLineHeight($font-size);
}

@mixin fontSmoothing($value: on) {
    @if $value == on {
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }
    @else {
        -webkit-font-smoothing: subpixel-antialiased;
        -moz-osx-font-smoothing: auto;
    }
}

/*********************
 * RATIO
 ********************/

 @mixin ratio($percentage) {
    width: 100%; height: 0;
    padding-top: $percentage;
}

/*********************
 * FORMS
 *********************/

@mixin placeholder($color: $input-color-placeholder) {
	&::-moz-placeholder {
		color: $color;
		opacity: 1;
	}
	&:-ms-input-placeholder { color: $color; }
	&::-webkit-input-placeholder  { color: $color; }
}
