/*********************
 * GLOBAL
********************/

.icon {
    &.icon-round {
        height: 22px; width: 22px;
        border-radius: 50%;
        display: block;

        background: $black;
        color: $white;
        font-size: 18px;
        line-height: 22px;
        text-align: center;

        transition: background $duration;
    }

