/******************************************************************

Stylesheet: Quantity Selectors

******************************************************************/

/*********************
 * GLOBAL
 ********************/

.quantity-selector-container {
    @include clearfix;

    .label {
        font-weight: $fw-lg;
        padding-right: 0; margin-right: 10px;
        width: 70px;
        line-height: 40px;
        float: left;
    }

    .quantity-selector {
        border: 1px solid $ofs-grey;
        border-radius: $border-radius-xs;
        height: 40px; 
        margin: 0!important;
        float: left;

        transition: border $duration;

        &:hover {
            border: 1px solid $ofs-darkgrey; 
        }

        button {
            cursor: pointer;
        }

        .minus, .plus, .qty {
            height: 38px; width: 40px;
            display: inline-block;
            vertical-align: top;

            .icon {
                width: 100%;
                line-height: 40px;
                padding: 0 5px;
            }
        }

        .qty {
            border: none;
            padding: 10px 0;
            text-align: center;
            width: 32px; min-height: 38px;
        }
    }
}
