/******************************************************************

Stylesheet: Forms

******************************************************************/

/*********************
 * GLOBAL
 ********************/

form, .form-group {
    input, textarea, .select2 .select2-selection {
        font-family: $font-family-base;
        display: block;
        width: 100%; min-height: 43px;
        border: 1px solid $ofs-grey;
        border-radius: $border-radius-xs;
        padding: 10px 15px;
        font-size: 14px;
        color: $ofs-darkgrey;

        transition: border-color $duration;

        &:hover {
            border-color: $ofs-darkgrey;
        }

        &:active, &:focus {
            border-color: $ofs-darkgrey;
            outline: none;
        }
    }

    label {
        margin-bottom: 5px;

    }

    abbr {
        text-decoration: none !important;
        cursor: default;
    }

    legend { display: block; }

    &.error {
        input, textarea {
            @include placeholder($ofs-danger);
            border-color: $ofs-danger;
        }

        label, small {
            color: $ofs-danger;
        }
    }
}

/*********************
 * STATES
 ********************/

.woocommerce form .form-row.woocommerce-validated {
    .select2-container, input.input-text, input.input-text, label {
        color: $ofs-darkgrey;
        border-color: $ofs-grey;
    }
}

.woocommerce form .form-row.woocommerce-invalid {
    .select2-container, input.input-text, input.input-text, label {
        color: $ofs-danger;
        border-color: $ofs-danger;
    }
}

/*********************
 * SELECT2
 ********************/

.select2 .select2-selection {
    font-family: $font-family-secondary;
    height: auto;
}

.select2-container
.select2-selection--single
.select2-selection__rendered {
    padding: 0;
    line-height: 21px;
    color: $ofs-darkgrey;
}

.select2-container--default
.select2-selection--single
.select2-selection__arrow {
    font-family: offsiderz!important;
    height: 43px!important; width: 40px!important;
    top: 0!important; right: 0!important;

    &::before {
        position: absolute; top: 50%; left: 50%;
        transform: translate(-50%, -50%);

        font-size: 24px;

        content: "\e313";
    }

    b {
        display: none;
    }
}

.select2-container--focus {
    outline: none !important;
}

.select2-container--default .select2-results__option--highlighted[aria-selected], 
.select2-container--default .select2-results__option--highlighted[data-selected] {
    background: $ofs-danger !important;
}

.woocommerce-ordering {
    float: none;
    width: 100%;

    .select2-container {
        width: 100%!important;

        .select2-selection--single {
            height: auto;
        
            .select2-selection__rendered {
                line-height: 21px;
            }
        }
    }
}

/*********************
 * WOOCOMMERCE
 ********************/

.woocomerce-form {
    label { margin-bottom: 5px; }
}

/*********************
 * LOGIN
 ********************/

#customer_login {}

/*********************
 * UI CHECKBOXES
 ********************/

.ui.checkbox {
    @include clearfix;

    position: relative;
    margin-bottom: $spacing-xs;

    input {
        position: absolute;
        opacity: 0;
        visibility: hidden;

        &:focus + label::before {
            border: 2px solid $ofs-darkgrey;
            outline: none !important;
        }

        & + label::after {
            position: absolute; left: 0; top: calc(50% - 12px);
            width: 25px; height: 25px;

            font-family: 'offsiderz';
            font-size: 25px;
            line-height: 25px;
            color: $ofs-darkgrey;
            text-align: center;

            content: "\e907";

            opacity: 0;
            transition: opacity $duration;
        }

        &:checked + label::after {
            opacity: 1;
            // transform: scale(1);
        }
    }

    label {
        position: relative;
        float: left;
        padding-left: 35px;
        line-height: 24px;

        // fake checkbox
        &:before {
            position: absolute; left: 0; top: calc(50% - 12px);
            background: $white;
            border: 1px solid #c6c6c6;
            border-radius: 0.1875rem;
            box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.25);

            width: $spacing-md; height: $spacing-md;
            display: block;
            float: left;
            margin-right: 0.75rem;
            content:'';
        }

        &.disabled {
            color: $ofs-lightgrey;
            opacity: 0.7;
            cursor: not-allowed;
        }
    }
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    .ui-checkbox input + label::after {
        top: 10px;
    }
}


/*********************
 * UI RADIOS
 ********************/

.ui.radio {
    display: inline-block;
    margin-top: $spacing-xs;
    width: 100%;

    & + .custom-radio { margin-top: $spacing-xs; }

    input[type=radio] {
		height: 0; width: 0;
        opacity: 0;
		display: none;
	    float: left;

        &:focus + label::before {
			border: 1px solid $ofs-xdarkgrey;
			outline: none !important;
		}

        &:checked + label::after {
            transform: scale(1,1);
        }
    }

    label {
        position: relative;
        padding-left: 0; margin-left: 0 !important;
        line-height: 25px;
        float: left;

        &:before {
            background: $white;
            border: 1px solid #c6c6c6;
            box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.25);

            width: $spacing-md; height: $spacing-md;
            border-radius: 50%;
            display: block;
            float: left;
            margin-right: 0.75rem;
            content:'';
        }

        &:after {
            position: absolute; left: 7px; top: 7px;
            width: 11px; height: 11px;
            background-color: $ofs-xdarkgrey;
            border-radius: 50%;
            transform: scale(0,0);
            transition: transform $duration $easeOutBack;
            content: '';
        }
    }

    &.inline {
        display:inline-block;
        margin-right: 30px;
        width: auto;
    }
}


/*********************
 * MEDIA QUERIES
 ********************/

// 576
@include media-breakpoint-up(xs) {

    .woocommerce-ordering {
        float: right;
        width: auto;

        .select2-container {
            min-width: 200px;
            width: auto!important;
        }
    }

}

// 768
@include media-breakpoint-up(sm) {}

// 992
@include media-breakpoint-up(md) {}

// 1200
@include media-breakpoint-up(lg) {}

// 1600
@include media-breakpoint-up(xlg) {}
