/******************************************************************

Stylesheet: Calendars

******************************************************************/

/*********************
 * GLOBAL
 ********************/

.calendar {
    position: relative;
    display: block;
    text-align: center;
    background: $ofs-darkblue;
    border-top-width: 12px;
    border-radius: 3px;
    width: 65px; height: 65px;
    
    &--inner {
        position: absolute; top: 13px; bottom: 3px; left: 3px; right: 3px;
        padding: 7px;
        background:$white;
        border-radius: 3px 3px 2px 2px;
    }
    
    &--day {
        font-family: $font-family-secondary;
        font-weight: $fw-lg;
        font-size: 28px;
        line-height: 26px;
        color: $ofs-darkblue;
        display: block; 
    }
    
    &--month {
        font-weight: $fw-lg;
        font-size: 12px;
        line-height: 12px;
        text-transform: uppercase;
        color: $ofs-darkblue;
        display: block; 
    }
}


