/******************************************************************

Stylesheet: Biography

******************************************************************/

/*********************
 * GLOBAL
 ********************/

.bio {
    background: $ofs-lightgrey;
    padding: 40px 0;

    &--visual {
        position: relative;
        margin-bottom: 15px;

        .bio--poster {
            @include fullscreen-bg;
            position: relative;
            width: 100%; height: 0;
            padding-top: 66.6%;
            background-color: $ofs-grey;

            img {
                position: absolute; top: 0; left: 0; right: 0; bottom: 0;
                width: 100%; height: 100%;
                display: block;
            }
        }

        // img {
        //     width: 100%; height: auto;
        // }
    }

    &--legend {
        @include fontSize(12px);
        position: absolute; bottom: 5px; right: 10px;
        color: $ofs-grey;
        opacity: .5;
        margin-bottom: 0;
    }

    &--content {
        text-align: left;

        p {
            font-weight: $fw-xs;
        }

        a {
            margin-top: 15px;
        }
    }
}

/*********************
 * MEDIA QUERIES
 ********************/

// 576
@include media-breakpoint-up(xs) {
    .bio {
        text-align: left;

        &--visual {
            display: inline-block;
            width: 50%; 
            vertical-align: middle;
            
            margin-bottom: 0;
        }
        
    
        &--content {
            display: inline-block;
            width: calc(50% - 15px); 
            vertical-align: middle;
            padding-left: 15px;
        }
    }
}

// 768
@include media-breakpoint-up(sm) {
    .bio {
        // padding: 65px 0;

        &--content {
            width: calc(50% - 25px); 
            padding-left: 25px;
        }
    }
}

// 992
@include media-breakpoint-up(md) {
    .bio {
        &--visual {
            width: 40%; 
        }
    
        &--content {
            width: calc(60% - 35px); 
            padding-left: 35px;

            a {
                margin-top: 25px;
            }
        }
    }
}

// 1200
@include media-breakpoint-up(lg) {
    .bio {
        &--content {
            .ofs-lead {
                font-size: 26px;
                line-height: 36px;
            }
        }
    }
}

// 1600
@include media-breakpoint-up(xlg) {
    .bio {
        &--content {
            width: calc(60% - 65px); 
            padding-left: 65px;

            .ofs-lead {
                font-size: 28px;
                line-height: 38px;
            }
        }
    }
}
